import React from 'react';
import './TvBoxControl.scss';
import NecButton from './NecButton';

export default () =>
  <table align="center">
    <tbody>
      <tr>
        <td><NecButton byte={64}>Power</NecButton></td>
        <td><NecButton byte={0b10010}>Mute</NecButton></td>
      </tr>
      <tr><td colspan="3"><hr /></td></tr>
      <tr>
        <td><NecButton byte={0b1001100}>Back</NecButton></td>
        <td><NecButton byte={0b1000111}>^</NecButton></td>
        <td><NecButton byte={0b1010110}>Menu</NecButton></td>
      </tr>
      <tr>
        <td><NecButton byte={0b1001000}>&lt;</NecButton></td>
        <td><NecButton byte={0b100100}>OK</NecButton></td>
        <td><NecButton byte={0b1001010}>&gt;</NecButton></td>
      </tr>
      <tr>
        <td/>
        <td><NecButton byte={0b10100}>Down</NecButton></td>
      </tr>
      <tr><td colspan="3"><hr /></td></tr>
      <tr>
        <td><NecButton byte={0b10000}>&lt;&lt;</NecButton></td>
        <td><NecButton byte={0b1000100}>||&gt;</NecButton></td>
        <td><NecButton byte={0b1000110}>&gt;&gt;</NecButton></td>
      </tr>
      <tr><td colspan="3"><hr /></td></tr>
      <tr>
        <td><NecButton byte={1}>1</NecButton></td>
        <td><NecButton byte={2}>2</NecButton></td>
        <td><NecButton byte={3}>3</NecButton></td>
      </tr>
      <tr>
        <td><NecButton byte={4}>4</NecButton></td>
        <td><NecButton byte={5}>5</NecButton></td>
        <td><NecButton byte={6}>6</NecButton></td>
      </tr>
      <tr>
        <td><NecButton byte={7}>7</NecButton></td>
        <td><NecButton byte={8}>8</NecButton></td>
        <td><NecButton byte={9}>9</NecButton></td>
      </tr>
      <tr>
        <td></td>
        <td><NecButton byte={0}>0</NecButton></td>
        <td></td>
      </tr>
    </tbody>
  </table>;
